import React from "react";

import FooterTitle from "../../titles/components/FooterTitle"

import styles from "../Footer.module.scss";

/**
 * 
 * @returns 
 */

function FooterAbout () {

    return (

        <div className={styles.__social__box}>
            <FooterTitle description="Sobre O Coselheiro" />
            <p className={styles.__social__about}>
                Seja muito bem-vindo! O Coselheiro - a loja -  a loja online de uma empresa que nasceu para contribuir para uma melhor qualidade de vida dos ces e seus familiares humanos. Seus scios so profissionais de educao canina que trabalham seguindo metodologias positivas de treinamento animal, encorajando e recompensando os acertos de nossos amigos em seus processos de adestramento. Aqui voc encontrar apenas itens de qualidade comprovada e utilizados pessoalmente por seus idealizadores. Fique  vontade!
            </p>
        </div>
    );
}

export default FooterAbout;